body {
	min-height: 100vh;
	padding: 5vh 5vw;
	display: flex;
	justify-content: center;
	align-items: center;
	background: #38074a;
	font-family: sans-serif;
}

.poster {
	position: relative;
	overflow: hidden;
	background-color: #086374;
	box-shadow: 0 20px 30px rgba(0, 0, 0, 0.3);
	/*
		Everything in % so it's fully responsive!!!!!
	*/
}
@media (min-aspect-ratio: 24/36) {
	.poster {
		width: 60vh;
		height: 90vh;
	}
}
@media (max-aspect-ratio: 24/36) {
	.poster {
		width: 90vw;
		height: 135vw;
	}
}
