.poster .circle-container {
	position: relative;
	width: 100%;
	height: 100%;
	pointer-events: none;
}
.poster .circle-container .circle-one {
	position: absolute;
	top: 26%;
	left: 30%;
	width: 50%;
	height: 33%;
	background-color: transparent;
	border-radius: 50%;
	border: 0 solid #3B1542;
}
@media (min-aspect-ratio: 24/36) {
	.poster .circle-container .circle-one {
		border-width: 4.5vh;
	}
}
@media (max-aspect-ratio: 24/36) {
	.poster .circle-container .circle-one {
		border-width: 5vw;
	}
}
.poster .circle-container .half-circle-one {
	position: absolute;
	z-index: 2;
	top: 29%;
	left: 32%;
	width: 45%;
	height: 15%;
	background-color: transparent;
	border-top-left-radius: 110px;
	/* 100px of height + 10px of border */
